import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import BookDetail from './BookDetail';

function BookInfo() {
    const { bookId } = useParams(); // URL  bookId 
    const [loading, setLoading] = useState(true); //     
    const [bookInfo, setBookInfo] = useState(null); //     

    const getBookInfo = async () => {
        try {
            // axios.get()  
            const response = await axios.get(`/info/bookList/bookInfo/${bookId}`);
            //const response = await axios.get(`/info/bookList/bookInfo`, { params: { bookId } });
            // response.data   
            const data = response.data;

            //   
            setBookInfo(data);
            setLoading(false); //   false 

        } catch (error) {
            //  
            console.error("Error fetching book info:", error);
        }
    };

    useEffect(() => {
        getBookInfo(); //    getBookInfo  
    }, [bookId]); // bookId   getBookInfo  

    return (
        <div>
            {loading ? (
                <h2>Loading...</h2> //     
            ) : (
                bookInfo && ( // bookInfo   BookDetail  
                    <BookDetail
                        bookId={bookInfo.bookId}
                        title={bookInfo.title}
                        description={bookInfo.description}
                        author={bookInfo.author}
                    />
                )
            )}
        </div>
    );
}

export default BookInfo;
