import React from 'react';
import '../../../css/bookInfo.css';


const BookDetail = ({ bookId, title, description, author,coverImageUrl,publishDate,format,pages,isbn,discountPrice,purchaseLink,price }) => {
    return (
        <div className="book-details">
            <div className="book-header">
                <img src={"../../../../public/images/logo.svg"} alt="Logo" className="book-cover"/>

                <div className="book-meta">
                    <h1>{title}</h1>
                    <div className="book-meta-details">
                        <p><strong>:</strong> {author}</p>
                        <p><strong>:</strong> {publishDate}</p>
                        <p><strong>:</strong> {format}</p>
                        <p><strong>:</strong> {pages}</p>
                        <p><strong>ISBN:</strong> {isbn}</p>
                        <p><strong>:</strong> {price}</p>
                        <p><strong>:</strong> {discountPrice}</p>
                    </div>
                    <a href="https://www.naver.com" className="purchase-button">  </a>
                </div>
            </div>
            <div className="book-description">
                <h2> </h2>
                <p>{description}</p>
            </div>
        </div>
    );
};

export default BookDetail;