import React, { useState, useEffect } from 'react';
import axios from 'axios';
import "../../../../css/noticeList.css"
import {Link} from "react-router-dom";

function NoticeList() {
    const [searchTerm, setSearchTerm] = useState('');
    const [searchType, setSearchType] = useState('title');
    const [filteredNotices, setFilteredNotices] = useState([]);

    useEffect(() => {
        //    
        const fetchNotices = async () => {
            try {
                const response = await axios.get('/info/noticeList');
                setFilteredNotices(response.data);
            } catch (error) {
                console.error('Error fetching notices:', error);
            }
        };
        fetchNotices();
    }, []);

    //    
    const searchNotice = () => {
        const filtered = filteredNotices.filter(notice =>
            searchType === 'writer'
                ? notice.writer.toLowerCase().includes(searchTerm.toLowerCase())
                : notice.title.toLowerCase().includes(searchTerm.toLowerCase())
        );

        setFilteredNotices(filtered);
    };
    const activeEvent=(e)=>{
        if(e.key==='Enter'){
            searchNotice();
        }
    }

    return (
        <div className="notice-list-container">
            <h1 className="title"></h1>
            <nav className="navbar">
                <ul className="nav-tabs">
                    <li className="nav-item "><a href="/customerCenter"></a></li>
                    <li className="nav-item active"><a href="/noticeList"></a></li>
                    <li className="nav-item"><a href="/questionAndAnswer"> </a></li>
                    <li className="nav-item"><a href="/faqList">  </a></li>
                </ul>
            </nav>
            <br/>
            <div className="search-sort-bar">
                <select
                    value={searchType}
                    onChange={(e) => setSearchType(e.target.value)}
                    className="search-select"
                >
                    <option value="title"></option>
                    <option value="writer"></option>
                </select>
                <input
                    type="text"
                    placeholder=" "
                    value={searchTerm}
                    onKeyDown={(e)=>activeEvent(e)}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="search-input"
                />
                <button className="search-button" onClick={searchNotice}></button>
            </div>
            <ul className="notice-list">
                {filteredNotices.map((notice) => (
                    <li key={notice.noticeId} className="notice-item">
                        {/*<div className="notice-id">{notice.noticeId}</div>*/}
                        <div className="notice-info">
                            <span className="notice-category">| {new Date(notice.date).toLocaleDateString()}</span>
                            <div className="notice-header">
                                <h4 className="notice-title">
                                    <Link className="remove-decoration" to={`/noticeDetail/${notice.noticeId}`}>{notice.title}</Link>
                                </h4>
                                <span className="notice-author"> : {notice.writer}</span>
                            </div>
                        </div>

                    </li>
                ))}
            </ul>
        </div>
    );
}

export default NoticeList;
