import React from 'react';
import { Link } from "react-router-dom";
import { useAuth } from '../pages/index/AuthContext';
import "../css/headerStyle.css";
import "../css/headerBoot.css"
import "../css/header.css"

const Header = () => {
    const { isAuthenticated, removeAuthToken } = useAuth();

    return (
        <section id="header">
            <div className="header-area">
                <div className="header_menu text-center" data-spy="affix" data-offset-top="50" id="nav">
                    <div className="container">
                        <nav className="navbar navbar-default zero_mp ">
                            <div className="navbar-header">
                                <button type="button" className="navbar-toggle collapsed" data-toggle="collapse"
                                        data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                                    <span className="sr-only">Toggle navigation</span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                </button>
                                <a className="navbar-brand custom_navbar-brand" href="#"><img src="img/logo.png"
                                                                                              alt=""/></a>
                            </div>
                            <div className="collapse navbar-collapse zero_mp" id="bs-example-navbar-collapse-1">
                                <ul className="nav navbar-nav navbar-right main_menu">
                                    <li><Link to="/"></Link></li>
                                    <li className="dropdown">
                                        <Link to="/bookList"></Link>
                                        <ul className="dropdown-content">
                                            <li><Link to="/bookList"></Link></li>
                                            <li><Link to="/bookPurchase"></Link></li>
                                        </ul>
                                    </li>
                                    <li><Link to="/events"></Link></li>
                                    <li className="dropdown">
                                        <Link to="/customerCenter"></Link>
                                        <ul className="dropdown-content">
                                            <li><Link to="/customerCenter"></Link></li>
                                            <li><Link to="/noticeList"></Link></li>
                                            <li><Link to="/questionAndAnswer"></Link></li>
                                        </ul>
                                    </li>
                                    {isAuthenticated ? (
                                        <li className="auth-links">
                                            <Link onClick={removeAuthToken} className="small-font bottom-align"></Link>
                                        </li>
                                    ) : (
                                        <>
                                            <li className="auth-links">
                                                <Link to="/signIn" className="small-font bottom-align"></Link>
                                            </li>
                                            <li className="auth-links">
                                                <Link to="/signup" className="small-font bottom-align"></Link>
                                            </li>
                                        </>
                                    )}
                                </ul>
                                <ul>

                                </ul>
                            </div>
                        </nav>
                    </div>
                </div>
            </div>

        </section>
    );
};

export default Header;
