import React, { useState } from "react";
import axiosInstance from "../AxiosInstance";
import { Navigate } from "react-router-dom";

const SignIn = () => {
    const [loginInput, setLoginInput] = useState({
        userId: "",
        password: "",
    });
    const [isLoggedIn, setIsLoggedIn] = useState(false);

    const handleInputChange = (e) => {
        setLoginInput((prev) => ({ ...prev, [e.target.name]: e.target.value }));
    };

    const onLogin = async (e) => {
        e.preventDefault(); //    
        try {
            const res = await axiosInstance.post('/customer/matchLogin', loginInput);

            if (res.data && res.data.authToken) {
                localStorage.setItem('authToken', res.data.authToken.accessToken); //    
                setIsLoggedIn(true); //   true 
            } else {
                throw new Error("Invalid response format");
            }
        } catch (error) {
            console.error("Login error:", error);
            alert(" : " + (error.response?.data?.message || " "));
        }
    };

    if (isLoggedIn) {
        return <Navigate to="/" replace />;
    }

    return (
        <>
            <h2></h2>
            <form onSubmit={onLogin}>
                <div>
                    <label htmlFor="userId"></label>
                    <input
                        type="text"
                        id="userId"
                        name="userId"
                        value={loginInput.userId}
                        onChange={handleInputChange}
                        placeholder=""
                        required
                    />
                </div>
                <div>
                    <label htmlFor="password"></label>
                    <input
                        type="password"
                        id="password"
                        name="password"
                        value={loginInput.password}
                        onChange={handleInputChange}
                        placeholder=""
                        required
                    />
                </div>
                <button type="submit"></button>
            </form>
        </>
    );
};

export default SignIn;
