import React, { useState, useEffect } from 'react';
import '../../../css/signUpForm.css'; // CSS  import
import axios from 'axios';
import Modal from 'react-modal';
import { LoadScript, Autocomplete } from '@react-google-maps/api';

const libraries = ["places"];

const SignUpForm = () => {
    const [formData, setFormData] = useState({
        userId:'',
        userName: '',
        email: '',
        password: '',
        confirmPassword: '',
        telNo: '',
        address: '',
        zipcode: ''
    });

    const [passwordMatch, setPasswordMatch] = useState(true);
    const [autocomplete, setAutocomplete] = useState(null);
    const [modalIsOpen, setModalIsOpen] = useState(false);

    useEffect(()=>{
        setPasswordMatch(formData.password===formData.confirmPassword);
    },[formData.password,formData.confirmPassword]);


    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (passwordMatch) {
            try {
                const response = await axios.post('/info/signUp', formData);
                console.log(' :', formData);
                alert(" .");
                window.location.href = '/';
            } catch (error) {
                console.error(' :', error);
                if (error.response && error.response.data) {
                    //     .
                    alert(` : ${error.response.data.message}`);
                } else {
                    //         .
                    alert(" :     .");
                }
            }
        } else {
            alert("  .");
        }
    };

    const handlePlaceSelect = () => {
        if (autocomplete !== null) {
            const place = autocomplete.getPlace();
            if (place.address_components) {
                const address = place.formatted_address;
                let zipCode = '';
                for (const component of place.address_components) {
                    const componentType = component.types[0];
                    if (componentType === 'postal_code') {
                        zipCode = component.long_name;
                    }
                }
                setFormData({
                    ...formData,
                    address: address,
                    zipCode: zipCode
                });
                setModalIsOpen(false); //     
            }
        } else {
            console.log('Autocomplete is not loaded yet!');
        }
    };
    const onLoad = (autocompleteInstance) => {
        setAutocomplete(autocompleteInstance);
    };

    const openModal = () => {
        setModalIsOpen(true);
    };

    const closeModal = () => {
        setModalIsOpen(false);
    };

    return (
        <div className="signup-container">
            <h2></h2>
            <form onSubmit={handleSubmit} className="signup-form">
                <div className="form-group">
                    <label htmlFor="userId"></label>
                    <input
                        type="text"
                        id="userId"
                        name="userId"
                        value={formData.userId}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="password"></label>
                    <input
                        type="password"
                        id="password"
                        name="password"
                        value={formData.password}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="confirmPassword"> </label>
                    <input
                        type="password"
                        id="confirmPassword"
                        name="confirmPassword"
                        value={formData.confirmPassword}
                        onChange={handleChange}
                        required
                    />
                    {!passwordMatch && (
                        <p className="error-message">  .</p>
                    )}
                </div>
                <div className="form-group">
                    <label htmlFor="userName"></label>
                    <input
                        type="text"
                        id="userName"
                        name="userName"
                        value={formData.userName}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="email"></label>
                    <input
                        type="email"
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="telNo"></label>
                    <input
                        type="text"
                        id="telNo"
                        name="telNo"
                        value={formData.telNo}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="address"></label>
                    <input
                        type="text"
                        id="address"
                        name="address"
                        value={formData.address}
                        readOnly
                        placeholder="  "
                        onClick={openModal}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="zipCode"></label>
                    <input
                        type="text"
                        id="zipCode"
                        name="zipCode"
                        value={formData.zipCode}
                        readOnly
                        required
                    />
                </div>
                <button type="submit" className="submit-button"></button>
            </form>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                contentLabel=" "
                ariaHideApp={false}
                className="address-modal"
            >
                <h2> </h2>
                <LoadScript googleMapsApiKey="AIzaSyB7XvKzrLlaghLGZitW9dBR7Z7DiP31Zr0" libraries={libraries}>
                <Autocomplete
                        onLoad={onLoad}
                        onPlaceChanged={handlePlaceSelect}
                    >
                        <input
                            type="text"
                            id="autocomplete"
                            placeholder=" "
                        />
                    </Autocomplete>
                </LoadScript>
                <button onClick={closeModal} className="close-button"></button>
            </Modal>
        </div>
    );
};

export default SignUpForm;
